<template>
  <div></div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "PreApp",
  components: {},
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      token: "auth/getToken",
    }),
  },
  created() {
    this.setTokenInApiCall();
  },
  methods: {
    setTokenInApiCall() {
      if (this.token != null) {
        this.$http.defaults.headers.common = {
          Authorization: `Bearer ${this.token}`,
        };
      }
    },
  },
};
</script>
